import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import ApiManager from './ApiManager'
import PropTypes from 'prop-types'


var sectionStyle = {
  width: "100%",
  height: "100%",
  borderRadius: "5px",
};

var logoStyle = {
  width: "80%",
  "padding-left": "4rem",
  "padding-bottom": "1rem"
}

class Register extends Component {
  constructor(props) {
    super(props)
    this.state = {
      username: '',
      password: '',
      email: '',
      firstName: '',
      lastName: '',
      birthdate: Register.formatDate(Date.now()),
      alertMessage: '',
    }
  }

  static formatDate(date) {
    const d = new Date(date)
    let month = '' + (d.getMonth() + 1)
    let day = '' + d.getDate()
    let year = '' + d.getFullYear()

    year = year.padStart(4, '0')
    month = month.padStart(2, '0')
    day = day.padStart(2, '0')

    return [year, month, day].join('-')
  }

  onChange = event => {
    const target = event.target
    const value = target.value
    const name = target.name
    this.setState({
      [name]: target.type === 'date' ? Register.formatDate(value) : value,
    })
  }

  submit = e => {
    e.preventDefault()
    const date = new Date(this.state.birthdate)
    ApiManager.register(
      this.state.username,
      this.state.password,
      this.state.email,
      this.state.firstName,
      this.state.lastName,
      date.toString(),
    )
      .then(() => {
        this.props.history.push('/connexion')
      })
      .catch(errorMessage => {
        const state = {
          alertMessage: 'Une erreur est survenue',
        }
        this.setState(state)
        window.scrollTo(0, 0)
      })
  }

  render() {
    return (
      <div>
        <img style={logoStyle} src={this.context.apiurl + "/static/logo_sportsfun.png"} alt="logo" />

        <div style={sectionStyle}>
          <div className="pagecontainer p-sm-5 ml-0">
            {this.state.alertMessage && (
              <p className="alert alert-danger">{this.state.alertMessage}</p>
            )}
            <form onSubmit={this.submit}>
              <div className="form-group">
                <label htmlFor="username">Nom d'utilisateur</label>
                <input type="text" name="username" className="form-control" id="username"
                  value={this.state.username} onChange={this.onChange} />
              </div>
              <div className="form-group">
                <label htmlFor="password">Mot de passe</label>
                <input type="password" name="password" className="form-control" id="password"
                  value={this.state.password} onChange={this.onChange} />
              </div>
              <div className="form-group">
                <label htmlFor="email">Adresse mail</label>
                <input type="email" name="email" className="form-control" id="email"
                  value={this.state.email} onChange={this.onChange} />
              </div>
              <div className="form-group">
                <label htmlFor="firstName">Prnom</label>
                <input type="text" name="firstName" className="form-control" id="firstName"
                  value={this.state.firstName} onChange={this.onChange} />
              </div>
              <div className="form-group">
                <label htmlFor="lastName">Nom</label>
                <input type="text" name="lastName" className="form-control" id="lastName"
                  value={this.state.lastName} onChange={this.onChange} />
              </div>
              <div className="form-group">
                <label htmlFor="birthdate">Date de naissance</label>
                <input type="date" name="birthdate" className="form-control" id="birthdate"
                  value={this.state.birthdate} onChange={this.onChange} />
              </div>
              <button type="submit" className="btn btn-outline-success">S'inscrire</button>
            </form>
            <p className="float-right mb-0">Vous avez dj un compte ? </p><Link to="/connexion" className="float-right btn btn-link">Cliquez ici pour vous connecter</Link>
          </div>
        </div >
      </div>
    )
  }
}

Register.contextTypes = {
  apiurl: PropTypes.string,
};
//<Link to="/connexion" className="btn btn-secondary mt-2">Se connecter</Link>

export default Register



